import React from 'react';
import './Display.css'


const Display = ({ display}) => {    
    const upperfontsize= parseInt(display.upperfontSize);
    const lowerfontsize= parseInt(display.lowerfontSize);
    
    const uslColor = 'https://res.cloudinary.com/dkkgmzpqd/image/upload/v1545217305/T-shirt%20Images/'   
    return (
        <div>
            <div className="card card-center">
                <div className="imgTshirt text-center">
                    <img className="img-responsive" src={`${uslColor}${display.tshirtColor}`} alt="img-Tshirt" />
                </div>
            </div>
            <div className="memText text-center">
                <div className="upperText">
                    <p style={{fontSize:upperfontsize,color:display.fontColor}}>{display.upperText}</p>
                </div>
                <div>
                    <img className="imgBack" src={display.memeImage} alt="Back" />
                </div>
                <div className="LowerText">
                    <p style={{fontSize:lowerfontsize,color:display.fontColor}}>{display.lowerText}</p>
                </div>
            </div>
        </div>
    )
}

export default Display;