import React from 'react';
import Display from '../design/Display';
import Settings from '../design/Setting'
import BackImage from '../../Image/400x300.png'

class Dashboard extends React.Component {

    state = {
        tshirtColor: 'black',
        upperText: 'This is Upper text',
        lowerText: 'This is Lower text',
        memeImage: BackImage,
        upperfontSize: 38,
        lowerfontSize: 38,
        fontColor: "white"
    }

    handleTshirtColor = (e) => {
        this.setState({ tshirtColor: e.target.id })
    }

    handleUpperText = (e) => {
        this.setState({ upperText: e.target.value })
    }

    handleLowerText = (e) => {
        this.setState({ lowerText: e.target.value })
    }

    handleImageUpload = (e) => {
        let reader = new FileReader();
        let file = e.target.files[0];

        reader.onloadend = () => {
            this.setState({ memeImage: reader.result });
        }
        reader.readAsDataURL(file);

    }

    handleUpperTextSize = (e) => {
        this.setState({ upperfontSize: e.target.value });
    }

    handleLowerTextSize = (e) => {
        this.setState({ lowerfontSize: e.target.value });
    }

    handleTextColor = (e) => {
        this.setState({ fontColor: e.target.value });        
    }

    render() {

        return (
            <div className="container py-5">
                <div className="row">
                    <div className="col-lg-8">
                        <Display
                            display={this.state}                           
                        />
                    </div>
                    <div className="col-lg-4">
                        <Settings
                            color={this.handleTshirtColor}
                            upText={this.handleUpperText}
                            lowerText={this.handleLowerText}
                            memeImage={this.handleImageUpload}
                            upperfontSize={this.handleUpperTextSize}
                            lowerfontSize={this.handleLowerTextSize}
                            textColor={this.handleTextColor}
                        />
                    </div>
                </div>
            </div>
        )
    }
}
export default Dashboard;