import React from 'react';

const urlImage = 'https://res.cloudinary.com/dkkgmzpqd/image/upload/v1545217305/T-shirt%20Images';

const Settings = ({ color, upText, lowerText, memeImage, upperfontSize, lowerfontSize, textColor }) => {
    return (
        <div className="card bg-light container">
            <h3 className="text-center">Setting</h3>
            <h4>Change T-shirt color</h4>
            <div className="tshirt-color">
                <img onClick={color} src={`${urlImage}/black.png`} alt="black Tshirt" id="black" />
                <img onClick={color} src={`${urlImage}/white.png`} alt="Whirt Tshirt" id="white" />
                <img onClick={color} src={`${urlImage}/grey.png`} alt="grey Tshirt" id="grey" />
                <img onClick={color} src={`${urlImage}/blue.png`} alt="blue Tshirt" id="blue" />
                <img onClick={color} src={`${urlImage}/red.png`} alt="red Tshirt" id="red" />
            </div>
            <hr />
            <h4>Write Text</h4>
            <input type="text" onChange={upText} className="form-control form-control-sm mb-2" placeholder="Uppder Text" />
            <input type="text" onChange={lowerText} className="form-control form-control-sm mb-2" placeholder="Lower Text" />
            <hr />
            <h4>Upload Image</h4>
            <div className="form-group">
                <input type="file" onChange={memeImage} className="form-control-file mb2" />
            </div>
            <hr />
            <h4>Upper Text Size</h4>
            <input type="range" min="12" max="52" onChange={upperfontSize} />
            <hr />
            <h4>Lower Text Size</h4>
            <input type="range" min="12" max="52" onChange={lowerfontSize} />
            <hr />
            <h4>Text color</h4>
            <select onChange={textColor} className="form-control form-control-sm mb2">
                <option>White</option>
                <option>Black</option>
                <option>Grey</option>
                <option>Blue</option>
                <option>Red</option>
                <option>lavender</option>
                <option>brown</option>
            </select>
            <hr />
            <button className="btn btn-primary btn-sm mb-2">Save</button>
            <div class='wrapper'>
                <button class='like-btn'>
                    <span class='like-text'>点赞</span>
                    <span>👍</span>
                </button>
            </div>


        </div>
    )
}

export default Settings;